<template>
    <div>
        <el-card class="box-card">
            <span>职员管理</span>
        </el-card>
        <el-row>
            <el-col :span="24">
                <el-form ref="form" :model="query" label-width="80px">
                    <el-form-item label="姓名">
                        <el-input v-model="query.name"></el-input>
                    </el-form-item>
                     <el-form-item label="职员类型">
                        <el-select v-model="query.region" placeholder="请选择">
                            <el-option v-for="(imet,index) in type" :key="index" :label="imet.name" :value="imet.id"></el-option>
                        </el-select>
                    </el-form-item>
                      <el-form-item>
                        <el-button type="primary">查询</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-button type="primary">添加</el-button>
            <el-button type="danger">批量删除</el-button>
            </el-row>
        <!-- 表格 -->
          <Table
            :loading="loading"
            :tableData="tableData"
            :tableCouml="tableCouml"
            @idshow="idshow">
            <template #sex="data">
                    <p>{{data.data.sex == 1?'男':'女'}}</p>
            </template>
            <template #option="data">
                <el-button size="mini" type="success" @click="mod(data.data)">修改</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(data.data)">删除</el-button>
            </template>
            </Table>

        <!-- 分页 -->
            <Paging :query="query" :total="total" @paging="paging"></Paging>

    </div>
</template>

<script>
import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";
export default {
    data(){
        return{
            loading: true,
            query:{
                page:1,
                psize:5
            },
            type:[],
            tableData: [],
            tableCouml: [
                { prop: "id", label: "ID",},
                { prop: "name", label: "姓名",},
                { prop: "typename", label: "职员类型",},
                { prop: "sex", label: "性别",isshow: true, showname: "sex"},
                { prop: "cardid", label: "身份证号",},
                { prop: "mobile", label: "手机号",},
                 { prop: "building", label: "管理小区",},
                { label: "操作", isshow: true, showname: "option" },
            ],
      total: 0,
        }
    },
    created(){
        this.gettype()
        this.getimet()
    },
    components:{
        Table,
        Paging,
    },
    methods:{
        paging(){
            this.getimet()
        },
        idshow(val){
            console.log(val)
        },
        // 职员类型
        async gettype(){
            let res = await this.http.get('/stafftype/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.type = res.data.list
            }
        },
        // 列表
        async getimet(){
            let res = await this.http.get('/staff/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.tableData = res.data.list
                this.loading = false
                this.total = res.data.counts
            }
        },
    }
}
</script>

<style lang="less" scoped>
@import '@/assets/css/admin.css';
</style>